<template>
  <div>
    <headerBar :header-data="headerData" />
    <div class="container">
      <div class="content">
        <div class="common-main box">
          <div style="width: 100%; height: 400px;">
            <amap
              ref="map"
              cache-key="map"
              view-mode="2D"
              async
              :zoom.sync="zoom"
              :center.sync="center"
              :pitch.sync="pitch"
              :rotation.sync="rotation"
              :show-indoor-map="false"
              is-hotspot
              @hotspotclick="onHotspotClick"
            >
              <amap-marker
                :position="position1"
                :label="{
                  content: '当前位置',
                  direction: 'bottom',
                }"
              >
                <div class="local-icon"> <i class="iconfont iconweizhi" /> </div>
              </amap-marker>
            <!-- <amap-
            marker :position="position2" :offset="[0, 0]" :anchor="anchor">
          <div class="marker-using-slot">使用slot的Marker</div>
        </amap-marker></amap> -->
            </amap>
          </div>
          <div class="dqwz">当前位置：{{ cdLable }}</div>
        </div>
      </div>
      <div class="foot-btn nobg noshadow">
        <ul>
          <li><van-button type="info" round size="large" @click="addLatAndLng()">上传坐标</van-button></li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script land="script" scoped>
import headerBar from '@/components/header/headerBar.vue'
import { pageRouter } from '@/common/js/mixin.js'
import { mapState } from 'vuex'
export default {
  components: {
    headerBar
  },
  mixins: [pageRouter],
  data() {
    return {
      headerData: {
        title: '上传电子坐标',
        leftUrl: 'back',
        leftText: ' '
      },
      center: [],
      position1: [],
      cdLable: '', // 场地信息
      zoom: 14,
      pitch: 45,
      rotation: 15
    }
  },
  computed: {
    // 取数据
    ...mapState({
      userInfo: state => state.mh.mhUserInfo
    })
  },
  mounted() {
    var self = this

    this.center.push(this.$route.params.centerLon)
    this.center.push(this.$route.params.centerLat)
    this.position1.push(this.$route.params.centerLon)
    this.position1.push(this.$route.params.centerLat)
    this.cdLable = this.$route.params.address
    console.info('updateLocation--ww---', self.center)
    console.info('updateLocation--ww---', self.position1)
    // window['updateLocation'] = function(value) {
    //   if (value !== '') {
    //     self.center.push(value.lon)
    //     self.center.push(value.lat)
    //     self.position1.push(value.lon)
    //     self.position1.push(value.lat)
    //     self.cdLable = value.address
    //     console.info('updateLocation--ww---', self.center)
    //     console.info('updateLocation--ww---', self.position1)
    //     // self.addLatAndLng()
    //   } else {
    //     console.info('updateLocation--2---', value)
    //     self.$toast.fail('定位数据获取失败！')
    //   }
    // }
    // this.getAddress()
  },
  methods: {
    getAddress() {
      const pin = {
        'type': '1'
      }
      if (process.env.NODE_ENV !== 'development') {
        try {
          window.myapp.getDistance(JSON.stringify(pin))
        } catch (err) {
          console.log(err)
        }
        this.$bridge.callhandler('getDistance', JSON.stringify(pin), (data) => {
          const datas = JSON.parse(data)
          // var lnglat = new AMap.LngLat(117.186602, 34.261579)// 测量点
          this.center.push(datas.lng)
          this.center.push(datas.lat)
          this.position1.push(datas.lng)
          this.position1.push(datas.lat)
          this.cdLable = datas.address
        })
      } else {
        this.$toast('返回原生页面')
      }
    },
    onHotspotClick(e) {
      if (e && e.lnglat) {
        this.center = [e.lnglat.lng, e.lnglat.lat]
      }
    },
    addLatAndLng() {
      console.info('this.userInfo---00---', this.userInfo)
      var baidu = this.bd_encrypt(this.center[0], this.center[1])
      this.$store.dispatch('erp/submitZbdzAction', {
        userId: this.userInfo.id,
        // longitude: 113.12353,
        // latitude: 47.231516,
        // address: 'this.cdLable'
        longitude: this.getBit(baidu.bd_lng),
        latitude: this.getBit(baidu.bd_lat),
        address: this.cdLable
      }).then((res) => {
        if (this.userInfo.yhzb !== null && this.userInfo.yhzb !== undefined && this.userInfo.yhzb !== '') {
          this.userInfo.yhzb.lng = this.getBit(baidu.bd_lng)
          this.userInfo.yhzb.lat = this.getBit(baidu.bd_lat)
        } else {
          var laction = { 'lng': this.getBit(baidu.bd_lng), 'lat': this.getBit(baidu.bd_lat) }
          this.userInfo.yhzb = { 'yhzb': laction }
        }

        console.info('this.userInfo------', this.userInfo)
        this.$toast.success('上传坐标成功！')
        // // this.$router.push({ name: 'MhHome' })
        // this.$router.push({ name: 'KqTj' })
      }).catch((error) => {
        this.$toast(error)
      })
    },
    getBit(value, bit = 7) {
      let str = value.toString()
      const strIndex = str.indexOf('.')
      if (strIndex === -1) return str
      str = str.substring(0, strIndex + bit)
      console.log(str, bit)
      return str
    },

    // 高德坐标转百度（传入经度、纬度）
    bd_encrypt(gg_lng, gg_lat) {
      var X_PI = Math.PI * 3000.0 / 180.0
      var x = gg_lng
      var y = gg_lat
      var z = Math.sqrt(x * x + y * y) + 0.00002 * Math.sin(y * X_PI)
      var theta = Math.atan2(y, x) + 0.000003 * Math.cos(x * X_PI)
      var bd_lng = z * Math.cos(theta) + 0.0065
      var bd_lat = z * Math.sin(theta) + 0.006
      return {
        bd_lat: bd_lat,
        bd_lng: bd_lng
      }
    }
  }
}
</script>

<style scoped>
.dqwz{margin: 10px;font-size: 14px;}
</style>
